.card{
    max-width: 300px;
    border: 1px solid black;
    border-radius: 8px;
    overflow: hidden;
}
.card-content{
    padding: 0 8px;

}
.card img{
    width: 100%;
}
.card h3,.card p{
    margin: 6px 0;
}
.container{
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}